export default function tabber() {
  const liall = document.querySelectorAll('.nav_item ul li')
  const content = document.querySelector('.content')
  const contentdata = ['生活不是一成不变','峨眉山真是令人向往的地方!','偶尔打一下游戏有益身心健康']
  //getAttribute获取自定义属性prop获取原生属性
  const items = []
  for (let i = 0; i < liall.length; i++) {
     const itemsContent =  document.createElement('div')
     itemsContent.style.display = 'none'
     itemsContent.innerHTML =  contentdata[i]
     content.appendChild(itemsContent)
     items.push(itemsContent)
  }
  items[0].className = 'itemsDeft'
  items[0].style.display = 'block'
  for (let i = 0; i < liall.length; i++) {
    liall[i].onclick = function() {
    for (let s = 0; s < items.length; s++) {
      if(items[s].className == 'itemsDeft') {
        items[s].classList.remove('itemsDeft')
        items[s].style.display = 'none'
      }
      }
      console.log(i)
      items[i].className = 'itemsDeft'
      items[i].style.display = 'block'
    }
  }
}